body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

a{
  text-decoration: none !important;
}
*{
  margin: 0;
  padding: 0;
}
/* NAVIGATION BAR */
.nav-container{
  width: 100%;
  display: flex;
  font: 30px Sans-serif;
  color: #FF335B;
  display: flex;
  margin: 15px;
}
.left-nav{
  width: 50%;
}
.right-nav{
  width: 50%;
  text-align: right;
  margin-right: 25px;
}
.right-nav Button{
  background-color: #FF335B;
}
.right-nav Button:hover{
  background-color: #FF335B;
  color: white;
}

/* NAVIGATION BAR */
.nav-bar {
    display: flex;
  }
  
  .left-nav {
    width: 50%;
  }
  
  .left-nav .logo {
    padding: 15px;
  }
  
  .left-nav .logo .logo-text h2 {
    color: #FF335B;
    font: 40px Sans-serif;
  }
  
  .right-nav {
    display: flex;
    width: 50%;
    margin-left: 650px;
  }
  
  .right-nav Button {
    margin: 15px;
    padding: 25px;
    height: 60px;
  }
  
  /* JOB CARD */
  .container {
    border: 1px solid #FF335B;
    border-radius: 15px;
    background-color: white;
    margin-left: 5px;
    justify-content: center;
    align-items: center;
    box-sizing: border-box;
    width: 700px;
    height: auto;
    margin-top: 10px;
    margin-bottom: 10px;
  }
  
  .header {
    display: flex;
  }
  .company-title{
    color: #FF335B;
    font-size: 30px;
  }
  
  .faint-text {
    display: flex;
  }
  .logo-text h1{
    color: #FF335B;
  }
*{
    margin: 0;
    padding: 0;
}
/* EMPLOYER REGISTRATION */
.registration-container{
    margin: 15px;
}
.input-formatting{
    box-sizing: border-box;
    margin: 25px;
}
.input-formatting h4{
    font: 24px Sans-serif;
    color: #FF335B;
}
.input-formatting .field1{
    width: 450px;
}
.comp-details{
    margin-right: 15px;
}
.submission-details{
    margin: 10px;
    display: flex;
}
.register{
    margin: 8px;
}
.register Button{
    background-color: #FF335B;
}
.login{
    margin: 8px;
}
.login Button{
    background-color: #C4BDBE;
    color: #000;
}

/* EMPLOYER LOGIN */
.login-container{
    margin-left: 600px;
    margin-top: 70px;
    border: 1.5px solid #FF335B;
    border-radius: 25px;
    width: 350px;
    height: 350px;
    padding: 50px;
    padding-left: 60px;
}
.email{
    margin-bottom: 5px;
}
.login-button Button{
    background-color: #FF335B;
    margin-top: 10px;
    margin-left: 73px;
}
.header-text{
    margin-left:12px;
    margin-bottom: 15px;
    font: 24px Sans-serif;
    color: #FF335B;
}
.alt-button Button{
    color: #B0B0B0;
}
.header h1{
    color: #FF335B;
    font-family: sans-serif;
}
